body .breed-select.is-hidden, body .cat-info.is-hidden {
  display: none;
}

body .cat-el {
  max-width: 740px;
  border: 1px solid rgba(65, 65, 65, .427);
  border-radius: 10px;
  margin: 0 auto;
  padding: 15px;
  box-shadow: 0 5px 8px rgba(0, 0, 0, .3);
}

body .cat-el p {
  letter-spacing: .02em;
  color: #1d1d31;
  font-family: Raleway, sans-serif;
  font-size: 1rem;
}

body .cat-el h4 {
  letter-spacing: .06em;
  color: #2d2d50;
  margin: 0 0 18px;
  font-family: Raleway, sans-serif;
  font-size: 1.9rem;
}

body .cat-el figure {
  aspect-ratio: 16 / 9;
  margin: 0;
  padding: 0;
}

body .cat-el img {
  max-width: 100%;
  border-radius: 10px;
}

body .backdrop {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, .416);
  justify-content: center;
  align-items: center;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

body .loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #fff;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid rgba(0, 0, 0, 0);
  border-left: 4px solid #ff3d00;
  border-radius: 50%;
  animation: rotation .5s linear infinite reverse;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

body .loader-text {
  font-size: 20px;
  font-weight: 600;
}

body.show-loader .backdrop {
  display: flex;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

